export default {
  data() {
    return {
      data: [],
    };
  },
  props: ["chartdata"],
  methods: {
    updateGraph() {
      this.chartdata.getFunFacts().then((funFacts) => (this.data = funFacts));
    },
  },
  watch: {
    chartdata: {
      handler() {
        this.updateGraph();
      },
      deep: true,
    },
  },
  mounted() {
    this.updateGraph();
  },
};
